/************ Default Css ************/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 75px;
}

body {
  font-weight: 400;
  background: linear-gradient(to bottom, #0C0B1B,#0e0d1c,#28003c,#0e0d1c,#28003c,#0e0d1c );
  /* background: linear-gradient(to bottom, #0C0B1B,#0e0d1c,#28003c,#502561,#502561,#9E2552 ); */
  /* ,#F14F68, #EA91AD, #F3A6AE,#F6D2B2 */
