}
.container{
padding-top: 5%;
padding-bottom: 5%;
}
h1{
  color: white;
  padding: 3%;
  text-shadow: 0 0 20px rgba(255, 255, 255, 0.8);
}
 h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  line-height: normal;
}

p, a, li, button, ul {
