}

.carousel-item {
  position: relative;
  display: flex;
  justify-content: center;
}

.carousel-item img {
  padding: 2%;
  max-height: 180px !important;
  width: auto;
  position: relative;
  background: #9fa2d879;
  border: 1px solid #9FA2D8;
  border-radius: 50px;
  margin-bottom: 5%;
}

.carousel-item .overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%; /* Set the width to 100% */
  opacity: 0;
  transition: 0.5s ease;
  background-color:#0a091e8b;
  border-radius: 50px;
  padding: 2%;
  margin-bottom: 5%;
}

.carousel-item:hover .overlay {
  opacity: 1;
}

.carousel-item .text {
  color: white;
  font-size: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

